//
// Accesability styles
// -----------------------------------------------------------------------------
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important; /* Many screen reader and browser combinations announce broken words as they would appear visually. */

	.screen-reader-text:focus {
		background-color: #f1f1f1;
		border-radius: 3px;
		box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
		clip: auto !important;
		clip-path: none;
		color: #21759b;
		display: block;
		font-size: 14px;
		font-size: 0.875rem;
		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000;
		/* Above WP toolbar. */
	}
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

//
// Alignment styles
// -----------------------------------------------------------------------------
.aligncenter {
	display: block;
	margin: 0 auto;
}

.alignleft {
	display: inline;
	float: left;
	margin: 0 $calc-line-height $calc-line-height 0;
}

.alignright {
	display: inline;
	float: right;
	margin: 0 0 $calc-line-height $calc-line-height;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.bypostauthor {
	display: block;
}
