//
// Layout variables
// -----------------------------------------------------------------------------
$screens: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px
);

$containers: (
	xs: none,
	sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1140px
);

$grid-columns: 12;
$grid-gutter-width: 60px;

//
// Typography variables
// -----------------------------------------------------------------------------
$text-font: 'Montserrat', serif;
$heading-font: 'Merriweather', sans-serif;
$code-font: monospace;

$base-font-size: 1rem;
$base-line-height: 1.875;
$heading-line-height: 1.3;
$type-scale: 1.414;

$calc-line-height: $base-font-size * $base-line-height;

$small-font-size: $base-font-size * 0.875;
$big-font-size: $base-font-size * 1.2;

$h3-font-size: $base-font-size * $type-scale;
$h2-font-size: $h3-font-size * $type-scale;
$h1-font-size: $h2-font-size * $type-scale;

//
// Color variables
// -----------------------------------------------------------------------------
$colors: (
	white: #FFFFFF,
	lightgray: #E4E4E4,
	darkgray: #555555,
	base: #272727,
	accent: #8C2849
);
//
// Form input variables
// -----------------------------------------------------------------------------
$input-height: $calc-line-height * 1.5;
$input-margin-bottom: $calc-line-height * 0.5;
$input-padding-x: 1rem;
$input-padding-y: ( $input-height / 2 - $calc-line-height / 2 );
