
#secondary{
	@extend .col-xs-12;
	@extend .col-md-4;

	padding: 0 $calc-line-height;
	section{
		padding-bottom: $calc-line-height*1.5;

		.widget-title{
			text-transform: uppercase;
		}
		&.widget_search{
			.widget-title{
				text-align: left;
			}
			form.search-form{
				position: relative;
				label{
					width: 100%;
				}
				.search-submit{
					position: absolute;
					top: 0;
					right: 0;
				}
			}
			@media screen and (min-width: map-get($screens, xs)) {
				text-align: center;
			}

		}

		&.widget_recent_entries, &.widget_recent_comments, &.widget_archive, &.widget_categories{
			select{
				height: 40px;
				border-radius: 0;
			}
			ul{
				list-style: none;
				margin: 0;
				li{
					padding: $calc-line-height/3 $calc-line-height/2;
					border-bottom: 1px solid map-get($colors, lightgray);
					a{
						border-bottom: none;
					}
				}
			}
		}
		&.widget_tag_cloud{
			a.tag-cloud-link{
				@extend .btn;
				font-size: $small-font-size !important;
				margin-right: $calc-line-height/4;
				border-radius: 5px;
				display: inline;
				line-height: $base-line-height*2;
			}
		}
	}
}
