//
// Default typography styles
// -----------------------------------------------------------------------------
html {
	font-size: 100%;
}

body,
button,
input,
select,
textarea {
	color: map-get( $colors, darkgray );
	font-family: $text-font;
	font-size: $base-font-size;
	line-height: $base-line-height;
}

//
// Block element styles
// -----------------------------------------------------------------------------
.block,
address,
blockquote,
dl,
figure,
.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6, h6,
hr,
ol,
p,
pre,
table,
ul {
	margin-bottom: $calc-line-height;
	margin-top: $calc-line-height;

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}
}

address,
blockquote {
	font-style: italic;
	padding-left: $calc-line-height;
}

blockquote {
	position: relative;

	&:before {
		border-left: thick solid map-get( $colors, lightgray );
		bottom: 0;
		content: '';
		display: block;
		left: 0;
		position: absolute;
		top: 0;
	}
}

dl {
	dt {
		font-weight: bold;
	}

	dd {
		margin-left: $calc-line-height;
	}
}

figure {
	height: auto;
	max-width: 100%;

	img {
		display: block;
		margin: 0 auto;
	}

	figcaption {
		font-size: $small-font-size;
		font-style: italic;
		line-height: $calc-line-height;
	}
}

.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6, h6 {
	font-family: $heading-font;
	font-size: $base-font-size;
	font-weight: bold;
	line-height: $heading-line-height;
}

.h1, h1 {
	font-size: $h1-font-size;
}

.h2, h2 {
	font-size: $h2-font-size;
}

.h3, h3 {
	font-size: $h3-font-size;
}

hr {
	border: 0;
	border-bottom: thin solid map-get( $colors, lightgray );

	&.thick {
		border-bottom-width: thick;
	}
}

ol,
ul {
	list-style: decimal;
	margin-left: $calc-line-height;

	ol,
	ul {
		margin-bottom: 0;
		margin-top: 0;
	}
}

ul {
	list-style: disc;
}

pre {
	background-color: map-get( $colors, lightgray );
	overflow: auto;
	padding: $calc-line-height;

	&, & * {
		font-family: $code-font;
		font-size: $base-font-size;
	}
}

table {
	border-collapse: collapse;
	border: thin solid map-get( $colors, lightgray );
	width: 100%;

	td,
	th {
		border: thin solid map-get( $colors, lightgray );
		padding: 0.5rem;
		text-align: left;
	}

	th {
		font-weight: bold;
	}
}

//
// Inline element styles
// -----------------------------------------------------------------------------
a {
	border-bottom: thin solid map-get( $colors, accent );
	color: map-get( $colors, accent );
	text-decoration: none;
	transition: all 1s;

	&:hover {
		opacity: 0.6;
	}
}

abbr[title],
acronym[title] {
	border-bottom: thin dotted map-get( $colors, darkgray );
	cursor: help;
	text-decoration: none;
}

b,
strong {
	font-weight: bold;
}

big {
	font-size: $big-font-size;
}

cite {
	font-weight: bold;
}

code,
kbd,
tt,
var {
	background-color: map-get( $colors, lightgray );
	border-radius: 0.25rem;
	display: inline-block;
	font-family: $code-font;
	padding: 0 0.5rem;
}

del,
s,
strike {
	text-decoration: line-through;
}

em,
i {
	font-style: italic;
}

img {
	height: auto;
	max-width: 100%;
}

ins,
u {
	border-bottom: thin solid map-get( $colors, darkgray );
	text-decoration: none;
}

q {
	font-style: italic;

	&:before {
		content: '\201c';
	}

	&:after {
		content: '\201d';
	}
}

small {
	font-size: $small-font-size;
}

sub,
sup {
	font-size: $small-font-size;
	position: relative;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}
