nav.navigation.pagination {
    text-align: center;
    padding: $calc-line-height ;

	a.page-numbers {
    	padding: $calc-line-height / 5 $calc-line-height / 3;
		color: map-get($colors,text-color);
		margin: 0 $calc-line-height/2;
		border: 1px solid map-get($colors, lightgray);
	}

	span.page-numbers.current{
		background: map-get($colors,accent);
		border: 1px solid map-get($colors, accent);
		padding: $calc-line-height / 5 $calc-line-height / 3;
		color: #fff;
	}

	a.prev.page-numbers, a.next.page-numbers{
		background: map-get($colors, lightgray);
		border-radius: unset;
		border: 1px solid map-get($colors, lightgray);
		padding: $calc-line-height/5 $calc-line-height / 3;
	}
}
