
/**
* Blog, Archive, Search page styling
*/

.blog, .archive, .search, .single, .page-template-default, .page-template-contact-page-template{
	.blog-page-heading, .archive-page-heading, .page-header{

		@extend .col-xs-12;
		@extend .col-md-12;

		padding-top: $calc-line-height*2;
		padding-bottom: $calc-line-height;

		.blog-page-title, .page-title{
			text-transform: uppercase;
			text-align: center;
		}
	}

	.single-post-content, .single-page-content{
		@extend .col-xs-12;
		@extend .col-md-8;

		.nav-links{
			.nav-previous, .nav-next{
				display: block;
				@media screen and (min-width: map-get($screens, md)) {
					width: 50%;
					display: inline-block;
					float: left;
				}

			}
			.nav-next{

				padding: $calc-line-height 0;
				@media screen and (min-width: map-get($screens, md)) {
					padding: $calc-line-height 0 $calc-line-height $calc-line-height ;
					text-align: right;
				}
			}
			.nav-previous{
				@media screen and (min-width: map-get($screens, md)) {
					padding: $calc-line-height $calc-line-height $calc-line-height  0;
				}
			}
		}
	}
}

.hentry{
	.blog &, .archive &, .search &{
		@extend .col-md-4;
	}

	padding-bottom: $calc-line-height;
	.entry-header{
		.entry-title{
			margin: 20px 0 10px 0;
		}
	}

	a {
		border-bottom: none;
	}

	hr{
		margin: $calc-line-height/2 0;
	}

	.entry-content{
		.excerpt-more{
			@extend .btn;
			text-transform: uppercase;
		}
	}
}

.single-content-row, .page-content-row{
	.single &, .page-template-default &, .page-template-contact-page-template &{
		padding-top: $calc-line-height * 2;
		padding-bottom: $calc-line-height * 2;
	}
}
.blog-post-grid .row{
	.blog &{
		@media screen and (min-width: map-get($screens, xs)) {
			padding: 0 $grid-gutter-width/2;
		}
	}
}
.error404{
	.site-content{
		text-align: center;
		.content-area{
			padding: $calc-line-height*2 0;
			.error-404{
				.page-header{
					padding: $calc-line-height*1.5 0;
				}
				.search-form{
					label{
						display: inline-block;
					}
					.search-submit{
						margin-left: -5px;
					}
				}
			}
		}
	}
}
