.comments-area{
	display: inline-block;

	ol.comment-list {
		padding: 0;
		margin: 0;
		list-style: none;
		li{
			list-style: none;
			border-top: 1px solid map-get($colors,lightgray);

			.altitude-lite-cmt-author-img{
				@extend .col-xs-12;
				@extend .col-md-3;

				padding: $calc-line-height $calc-line-height 0 $calc-line-height;
				@media screen and (min-width: map-get($screens, md)) {
					padding: $calc-line-height 0 $calc-line-height $calc-line-height ;
				}
			}

			.comment-content{
				@extend .col-xs-12;
				@extend .col-md-9;
				padding-bottom: $calc-line-height;
				@media screen and (min-width: map-get($screens, md)) {
					padding: $calc-line-height;
				}

				.altitude-lite-comment-text{
					margin: $calc-line-height/2 0;
				}
				.altitude-lite-cmt-author-meta{
					.altitude-lite-author-name, .commentmetadata{
						margin: 0;
					}
				}
				.altitude-lite-cmt-reply{
					@extend .btn;
					padding: $calc-line-height/6 $calc-line-height/4;

					a{
						font-size: $small-font-size;
						color: map-get($colors, white);
					}
				}
			}
		}

		.children{
			@media screen and (min-width: map-get($screens, md)) {
				padding-left: $calc-line-height * 1.5;
			}
		}
	}
}

.comment-respond{
	p label{
		font-style: normal;
	}
}

