//
// Site header styles
// -----------------------------------------------------------------------------
.site-header {
	.container {
		position: relative;
	}
}

//
// Brand styles
// -----------------------------------------------------------------------------
.site-branding {
	@media screen and (min-width: map-get($screens, xs)) {
		padding: 0;
	}
	@media screen and (min-width: map-get($screens, md)) {
		padding: $calc-line-height/2 0;
	}
	.site-logo{
		@extend .col-xs-12;
		@extend .col-lg-4;

		@media screen and (min-width: map-get($screens, xs)) {
			text-align: center;
		}

		@media screen and (min-width: map-get($screens, md)) {
			text-align: left;
		}
	}
	.contact-details {
		@extend .col-xs-12;
		@extend .col-lg-8;

		@media screen and (min-width: map-get($screens, xs)) {
			text-align: center;
			span{
				display: block;
			}
		}

		@media screen and (min-width: map-get($screens, md)) {
			text-align: right;
			span{
				display: inline-block;
				margin-left: $calc-line-height/2;
			}
		}
	}

	.site-title,
	.site-description {
		line-height: $calc-line-height;
		margin: 0;

		.wp-custom-logo & {
			@extend .screen-reader-text;
		}
	}

	.site-title {
		font-family: $heading-font;
		font-size: $h2-font-size;
		font-weight: bold;
		margin-top: $calc-line-height / 2;
	}

	.site-description {
		font-size: $small-font-size;
		font-style: italic;
		margin-bottom: $calc-line-height / 2;
	}

	a {
		border-bottom: transparent;
		color: map-get( $colors, darkgray );

		&:hover {
			color: map-get( $colors, accent );
		}
	}
}

//
// Main navigation styles
// -----------------------------------------------------------------------------
.header-menu {
	background-color: map-get( $colors, accent );
}

.main-navigation {
	@extend .col-xs-12;
	@extend .col-lg-12;

	font-family: $text-font;
	font-size: $base-font-size;
	letter-spacing: 0.0625rem;
	line-height: $calc-line-height;

	@media screen and (min-width: map-get($screens, md)) {
		padding-left: 0;
	}

	a{
		color: map-get( $colors, white );
	}

	li.menu-item-has-children {
		position: relative;
		color: map-get($colors, white);

		&:hover {
			//opacity: 0.8;
			& > .sub-menu {
				display: block;
			}
		}
	}

	.menu-toggle {
		@extend .show-xs;
		@extend .hide-lg;

		background-color: transparent;
		right: $grid-gutter-width / 2;
		top: $calc-line-height / 2;

		@media screen and (min-width: map-get($screens, xs)) {
			margin: 0 auto;
			color: map-get($colors, white);
		}
	}

	.menu {
		@extend .hide-xs;
		@extend .show-lg;
		@extend .clear;

		list-style: none;
		margin-left: 0;

		a {
			display: block;
			padding: ( $calc-line-height / 2 ) 0;
		}

		@media ( min-width: map-get( $screens, lg ) ) {
			a {
				padding: ( $calc-line-height / 2 ) 0;
			}

			ul {
				list-style: none;
				margin-left: 0;
			}

			li {
				float: left;
				margin-right: 2rem;
			}
		}
	}

	.sub-menu {
		@media ( min-width: map-get( $screens, lg ) ) {
			display: none;
			left: -1rem;
			letter-spacing: 0;
			line-height: $heading-line-height;
			position: absolute;
			text-transform: none;
			top: 100%;
			width: 200px;
			z-index: 1000;

			a {
				background-color: map-get( $colors, accent );
				border-bottom: thin solid map-get( $colors, white );
				padding-left: 1rem;
				padding-right: 1rem;
			}

			li {
				float: none;
				margin-left: 0;
			}

			.sub-menu {
				left: 101.5%;
				top: 0;
			}
		}
	}

	&.toggled {
		.menu {
			display: block;
		}
	}
}
