//
// Form styles
// -----------------------------------------------------------------------------
form p {
	margin-bottom: $input-margin-bottom;
	margin-top: $input-margin-bottom;
}

.btn,
button,
input,
select,
textarea {
	background-color: map-get( $colors, lightgray );
	border: 0;
	display: inline-block;
	padding: $input-padding-y $input-padding-x;
	width: 100%;
}

label {
	font-size: $small-font-size;
	font-style: italic;
}

textarea {
	height: $input-height * 3;
}

.btn,
button,
input[type=button],
input[type=checkbox],
input[type=radio],
input[type=submit] {
	width: auto;
}

.btn,
button,
input[type=button],
input[type=submit] {
	background-color: map-get( $colors, accent );
	color: map-get( $colors, white );
	cursor: pointer;
	font-family: $text-font;
	font-weight: normal;
	transition: all 1s;

	&:hover {
		opacity: 0.75;
	}
}
