//
// Container styles
// -----------------------------------------------------------------------------
.container {
	margin-left: auto;
	margin-right: auto;
	padding-left: $grid-gutter-width / 2;
	padding-right: $grid-gutter-width / 2;

	@each $screen, $width in $screens {
		@media ( min-width: $width ) {
			max-width: map-get( $containers, $screen );
		}
	}
}

//
// Row styles
// -----------------------------------------------------------------------------
.row {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-left: $grid-gutter-width / -2;
	margin-right: $grid-gutter-width / -2;

	&.align-items-center {
		align-items: center;
	}
}

//
// Column styles
// -----------------------------------------------------------------------------
@each $screen, $width in $screens {
	@media ( min-width: $width ) {
		@for $i from 1 through $grid-columns {
			.col-#{$screen}-#{$i} {
				padding-left: $grid-gutter-width / 2;
				padding-right: $grid-gutter-width / 2;
				width: percentage( $i / $grid-columns );
			}
		}
	}
}

//
// Show / hide styles
// -----------------------------------------------------------------------------
@each $screen, $width in $screens {
	@media ( min-width: $width ) {
		.hide-#{$screen} {
			display: none;
		}

		.show-#{$screen} {
			display: block;
		}
	}
}

//
// Clear floats styles
// -----------------------------------------------------------------------------
.clear {
	&:before,
	&:after {
		content: '';
		display: table;
	}

	&:after {
		clear: both;
	}
}
